import '@servicenow/now-card';
import '@servicenow/now-avatar';
import '@servicenow/now-highlighted-value';
import '@servicenow/now-heading';
import '@servicenow/now-label-value';
import { AGENT, LEFT, RIGHT } from '../constants';
import { msToTime } from '../utils/msToTime';
import get from 'lodash/get';


const renderCard = (transcriptObj, agentId, customerId) => {
	const person = get(transcriptObj, 'participantId', '');
	const time = msToTime(get(transcriptObj, 'beginOffsetMillis', ''));
	const content = get(transcriptObj, 'content', '');
	const highlightValue = get(transcriptObj, 'additionalInfo[0].value', '');
	const highlightColor = get(transcriptObj, 'additionalInfo[0].highlightColor', 'info');

	const side = (person === AGENT) ? RIGHT : LEFT;
	const userName = (person === AGENT) ? agentId : customerId;

	return (
		<div className="message-container">
			<div className={"avatar-time ".concat(side)}>
				<now-avatar userName={userName} imageSrc="" size="sm" />
				<now-label-value-inline label={time} className="time" />
			</div>
			
			<now-card size="md" className={side}>
				<now-card-header
					heading={{ label: <div>{content}</div>, size: 'sm', lines: 5, variant: 'tertiary' }}>
				</now-card-header>
				<now-card-footer>
					<now-highlighted-value label={highlightValue} status={highlightColor} />
				</now-card-footer>
			</now-card>
		</div>
	)
};


const view = (state) => {
	const transcript = get(state, 'properties.transcriptArray', '');
	const agentId = get(state, 'properties.agentId', '');
	const customerId = get(state, 'properties.customerId', '');

	return (
		<div className="transcript-container">
			<div>
				{transcript.map(transcriptObj => renderCard(transcriptObj, agentId, customerId))}
			</div>
		</div>
	);
};


export default view;
