import '@servicenow/now-empty-state';
import '@servicenow/now-image';
import '@servicenow/now-heading';
import '@servicenow/now-button';
import { EMPTY_STATE_CONSTANTS, SUCCESS, ERROR, IN_PROGRESS, PROCESSING, UNAVAILABLE } from '../constants';
import get from 'lodash/get';
import isNil from 'lodash/isNil';
// eslint-disable-next-line no-unused-vars
import { Fragment } from '@seismic/snabbdom-renderer';

const renderEmptyState = (emptyState, key, image_url) => {
	const line1 = get(EMPTY_STATE_CONSTANTS, `${emptyState}.line1.${key}`, EMPTY_STATE_CONSTANTS[emptyState]['line1']);
	const line2 = get(EMPTY_STATE_CONSTANTS, `${emptyState}.line2.${key}`, EMPTY_STATE_CONSTANTS[emptyState]['line2']);

	return (
		<div>
			<now-image src={image_url} alt={image_url} width="56" fit="cover" className="center" />
			<now-heading label={line1} level="2" variant="title-primary" className="center" />
			<now-heading label={line2} level="3" variant="title-secondary" className="center" />
		</div>
	);
};

const renderMonitorButton = (metadata) => {
	const callMonitorURL = get(metadata, 'callMonitorURL', '');
	if (!isNil(callMonitorURL)) {
		return (
			<div className="center">
				<a href={callMonitorURL} target="_blank">
					<now-button label="Monitor Call" variant="primary" size="md"></now-button>
				</a>
			</div>
		);
	} else {
		null;
	}
};

const renderContent = (state, transcriptState, recordingState) => {
	const metadata = get(state, 'properties.metadata', '');
	const emptyStateImages = get(state, 'properties.metadata.emptyStateImages', '');
	const recordingUrls = get(state, 'properties.recordingData.recordingUrls', '');
	const transcriptArray = get(state, 'properties.transcriptData.transcript', '');
	const agentId = get(state, 'properties.transcriptData.metadata.agentId', '');
	const customerId = get(state, 'properties.transcriptData.metadata.customerId', '');

	// render appropriate component/empty state based on transcriptState & recordingState
	if (transcriptState === IN_PROGRESS && recordingState === IN_PROGRESS) {
		return ( 
			<Fragment> 
				{ renderEmptyState(IN_PROGRESS, "both", emptyStateImages[IN_PROGRESS]) }
				{ renderMonitorButton(metadata) }
			</Fragment> 
		)
	} else if (transcriptState === PROCESSING && recordingState === PROCESSING) {
		return ( <Fragment> { renderEmptyState(PROCESSING, "both", emptyStateImages[PROCESSING]) } </Fragment> )
	} else if (transcriptState === UNAVAILABLE && recordingState === UNAVAILABLE) {
		return ( <Fragment> { renderEmptyState(UNAVAILABLE, "both", emptyStateImages[UNAVAILABLE]) } </Fragment> )
	} else if (transcriptState === PROCESSING && recordingState === SUCCESS) {
		return (
			<Fragment>
				<sn-cti-recording-player recordingUrls={recordingUrls} />
				{ renderEmptyState(PROCESSING, "transcript", emptyStateImages[PROCESSING]) }
			</Fragment>
		)
	} else if (transcriptState === SUCCESS && recordingState === PROCESSING) {
		return (
			<Fragment>
				{ renderEmptyState(PROCESSING, "recording", emptyStateImages[PROCESSING]) }
				<sn-cti-transcript-container transcriptArray={transcriptArray} agentId={agentId} customerId={customerId} />
			</Fragment>
		)
	} else if (recordingState === SUCCESS && transcriptState === UNAVAILABLE) {
		return (
			<Fragment>
				<sn-cti-recording-player recordingUrls={recordingUrls} />
				{ renderEmptyState(UNAVAILABLE, "transcript", emptyStateImages[UNAVAILABLE]) }
			</Fragment>
		)
	} else if (recordingState === UNAVAILABLE && transcriptState === SUCCESS) {
		return (
			<Fragment>
				{ renderEmptyState(UNAVAILABLE, "recording", emptyStateImages[UNAVAILABLE]) }
				<sn-cti-transcript-container transcriptArray={transcriptArray} agentId={agentId} customerId={customerId} />
			</Fragment>
		)
	} else if (recordingState === SUCCESS && transcriptState === SUCCESS) {
		return (
			<Fragment>
				<sn-cti-recording-player recordingUrls={recordingUrls} />
				<sn-cti-transcript-container transcriptArray={transcriptArray} agentId={agentId} customerId={customerId} />
			</Fragment>
		)
	}	
 };


const view = (state) => {
	const transcriptData = get(state, 'properties.transcriptData', '');
	const recordingData = get(state, 'properties.recordingData', '');
	const fetchSuccessful = get(state, 'properties.fetchSuccessful', '');

	if (fetchSuccessful) {
		let recordingState = get(state, 'properties.metadata.recordingState', '');
		let transcriptState = get(state, 'properties.metadata.transcriptState', '');
		
		// check for unexpected recording/transcript error, then update state
		if (transcriptState === SUCCESS && transcriptData.status === ERROR) {
			transcriptState = UNAVAILABLE;
		}
		if (recordingState === SUCCESS && recordingData.status === ERROR) {
			(transcriptState === PROCESSING) ? recordingState = PROCESSING : recordingState = UNAVAILABLE;
		}

		return (
			<div className="call-analysis-content">
				{ renderContent(state, transcriptState, recordingState) }
			</div>
		)
	} else {
		return (
			<now-empty-state type="loading" variant="small" />
		);
	}
};


export default view;
