/* eslint-disable no-console */
import { actionTypes } from '@seismic/core';
const { COMPONENT_BOOTSTRAPPED } = actionTypes;
import {
	FETCH_INTERACTION,
	FETCH_INTERACTION_SUCCEEDED,
	FETCH_INTERACTION_FAILED
} from './action';
import { SUCCESS } from '../constants';
import { createHttpEffect } from '@seismic/effect-http';
import get from 'lodash/get';

const fetchInteraction = createHttpEffect(
	'/api/sn_cti_core/cti_api/call_analysis/:interactionId',
	{
		method: 'GET',
		pathParams: ['interactionId'],
		successActionType: FETCH_INTERACTION_SUCCEEDED,
		errorActionType: FETCH_INTERACTION_FAILED
	}
);

const handleFetchInteractionSuccess = ({action, updateProperties}) => {
	const { recordingData, transcriptData, metadata, status } = action.payload.result;
	const fetchSuccessful = status === SUCCESS;

	updateProperties({
		recordingData,
		transcriptData,
		metadata,
		fetchSuccessful
	});
};

const handleFetchInteractionFail = () => {
	console.log('INTERACTION FETCH FAIL');
};

const actionHandlers = {
	[COMPONENT_BOOTSTRAPPED]: ({ dispatch, state }) => {
		const interactionId = get(state, 'properties.sysId', '');
		dispatch(FETCH_INTERACTION, { interactionId: interactionId });
	},
	[FETCH_INTERACTION]: fetchInteraction,
	[FETCH_INTERACTION_SUCCEEDED]: handleFetchInteractionSuccess,
	[FETCH_INTERACTION_FAILED]: handleFetchInteractionFail
};

export default actionHandlers;
