import '@servicenow/now-heading';
import '@servicenow/now-empty-state';
import '@servicenow/now-chart-timeseries';
import '@servicenow/now-chart-donut-pie';
import { ANALYSIS, COLORS, CHART_LABELS } from '../constants';
import get from 'lodash/get';
import { t } from 'sn-translate';


const view = (state) => {
    const sentimentTrend = get(state, 'properties.transcriptData.sentimentTrend', []);
    const lineChartData = sentimentTrend;
    const lineChartConfig = {Score:{x:"x", y:"y"}};

    const sentimentValuesMap = get(state, 'properties.transcriptData.sentimentPercents', {});
    const sentimentSeriesData = [];
    for (const [key, value] of Object.entries(sentimentValuesMap)) {
        sentimentSeriesData.push({name: CHART_LABELS[key], count: value});
    }
    const sentimentConfig = {
        "pie": {
            x:"name",
            y:"count",
            colorPalette:[COLORS.POSITIVE, COLORS.NEUTRAL, COLORS.NEGATIVE]
        },
    };

    const talkTimeMap = get(state, 'properties.transcriptData.talkTimePercents', {});
    const talkData = [];
    for (const [key, value] of Object.entries(talkTimeMap)) {
        talkData.push({name: CHART_LABELS[key], count: value});
    }
    const talkConfig = {
        "pie": {
            x:"name",
            y:"count",
            colorPalette:[COLORS.AGENT, COLORS.CUSTOMER, COLORS.NON]
        },
    };

    if (get(state, 'properties.fetchSuccessful', false)) {
        return (
            <div className="charts-container">
                <div className="title">
                    <now-heading label={ANALYSIS} level="4" variant="header-secondary"/>
                </div>
                <div className="chart">
                    <p className="chart-title">{t("Sentiment Trend")}</p>
                    <hr></hr>
                    <now-chart-timeseries
                        xAxisConfig={{ tickConfig: { tickValues: [] }, strokeWidth:0 }}
                        seriesData={lineChartData}
                        seriesConfig={lineChartConfig}
                        tooltipConfig={{}}
                        interactions={["showDataOnHover"]}
                        hideRegister={true}
                        width="auto"
                        height="auto"
                        yAxisType="linear"
                        yFormat=".1f" />
                </div>
                <div className="chart">
                    <p className="chart-title">{t("Customer Sentiment")}</p>
                    <hr></hr>
                    <now-chart-donut-pie
                        type="pie"
                        orderBy="dataOrder"
                        registerPosition="right"
                        registerConfig={{ yFormat: (d) => `${d.toFixed(0)}%`, 
                                            showPercentages:false,
                                            expandToFit:false }}
                        width="auto" height="150"
                        seriesData={sentimentSeriesData}
                        seriesConfig={sentimentConfig} />
                </div>
                <div className="chart">
                    <p className="chart-title">{t("Talk Time")}</p>
                    <hr></hr>
                    <now-chart-donut-pie
                        type="pie"
                        orderBy="dataOrder"
                        registerPosition="right"
                        registerConfig={{ yFormat: (d) => `${d.toFixed(0)}%`, 
                                            showPercentages:false,
                                            expandToFit:false }}
                        width="auto" height="150"
                        extraMargin={{"top":0,"bottom":0,"right":0,"left":34}}
                        seriesData={talkData}
                        seriesConfig={talkConfig} />
                </div>
            </div>                    
        );
    } else {
        return (
            <now-empty-state type="loading" variant="small" />
        );
    }
};

export default view;
